/**
 * props
 */
export const checkPropsData = ({value, min, max, step, tooltip}) => {
  if (max <= min) return warn(`${max}${min}`)
  if (value < min) return warn(`${value}${min}`)
  if (value > max) return warn(`${value}${max}`)
  if (step > max - min) return warn(`${max - min}`)
  if ((max - min) % step !== 0) return warn(`${max}${min}${max - min}${step}`)
  if (tooltip.indexOf('{value}') === -1) return warn('tooltip{value}')
  return true
}

function warn (str) {
  console.error(str)
  return false
}



