<template>
  <div :class="wrapClasses" :style="wrapStyles">
    <template v-if="type!=='textarea'">
      <div
        :class="[baseClass + '-group-prepend']"
        v-if="prepend"
        v-show="slotReady"
        ref="prepend">
        <slot name="prepend"></slot>
      </div>
      <transition name="fade">
        <icon :type="icon" :class="baseClass + '-icon'" v-if="icon" @click.native="onIconClick"></icon>
      </transition>
      <input
        :type="type"
        :class="inputClasses"
        :placeholder="placeholder"
        :disabled="disabled"
        :maxlength="maxlength"
        :readonly="readonly"
        :name="name"
        :number="number"
        :value="currentValue"
        @keyup.enter="onEnter"
        @focus="onFocus"
        @blur="onBlur"
        @input="onInput"
        @change="onChange">
      <div
        :class="[baseClass + '-group-append']"
        v-if="append"
        v-show="slotReady"
        ref="append">
        <slot name="append"></slot>
      </div>
    </template>
    <textarea
      v-else
      ref="textarea"
      :class="textareaClasses"
      :style="textareaStyles"
      :placeholder="placeholder"
      :disabled="disabled"
      :rows="rows"
      :maxlength="maxlength"
      :readonly="readonly"
      :name="name"
      :value="currentValue"
      @keyup.enter="onEnter"
      @focus="onFocus"
      @blur="onBlur"
      @input="onInput">
    </textarea>
  </div>
</template>

<script>
import Icon from '../icon/icon'
import oneOf from '../../utils/collection/one-of'
import calcTextareaHeight from '../../utils/dom/calc-textarea-height'

const baseClass = `x-input`

export default {
  name: 'XInput',

  props: {
    // 
    type: {
      validator (value) {
        return oneOf(value, ['text', 'textarea', 'password'])
      },
      default: 'text'
    },
    // 
    value: {
      type: [String, Number],
      default: ''
    },
    // 
    width: {
      type: Number
    },
    // 
    size: {
      validator (value) {
        return oneOf(value, ['small', 'large'])
      }
    },
    // 
    placeholder: {
      type: String,
      default: ''
    },
    // 
    maxlength: {
      type: Number
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    // 
    icon: String,
    // 
    autosize: {
      type: [Boolean, Object],
      default: false
    },
    // 
    rows: {
      type: Number,
      default: 2
    },
    // 
    readonly: {
      type: Boolean,
      default: false
    },
    // 
    name: {
      type: String
    },
    // 
    number: {
      type: Boolean,
      default: false
    }
  },

  components: {
    Icon
  },

  data () {
    return {
      baseClass,
      currentValue: this.value,
      prepend: true, // 
      append: true, // 
      slotReady: false,
      textareaStyles: {}
    }
  },

  computed: {
    // 
    wrapClasses () {
      return [
        `${baseClass}-wrapper`,
        {
          [`${baseClass}-wrapper--${this.size}`]: !!this.size,
          [`${baseClass}-${this.type}`]: this.type,
          [`${baseClass}-group`]: this.prepend || this.append,
          [`${baseClass}-group--${this.size}`]: (this.prepend || this.append) && !!this.size
        }
      ]
    },
    // 
    inputClasses () {
      return [
        `${baseClass}`,
        {
          [`${baseClass}--${this.size}`]: !!this.size,
          [`${baseClass}--disabled`]: this.disabled
        }
      ]
    },
    // 
    textareaClasses () {
      return [
        `${baseClass}`,
        {
          [`${baseClass}--disabled`]: this.disabled
        }
      ]
    },
    wrapStyles () {
      let result = {}
      if (this.width) {
        result.width = `${this.width}px`
      }
      return result
    }
  },

  mounted () {
    this.$nextTick(() => {
      this.initialize()
    })
  },

  watch: {
    value (val) {
      this.setCurrentValue(val)
    }
  },

  methods: {
    /**
     * 
     */
    initialize () {
      if (this.type !== 'textarea') {
        this.prepend = this.$slots.prepend !== undefined
        this.append = this.$slots.append !== undefined
      } else {
        this.prepend = false
        this.append = false
      }

      this.slotReady = true
      this.resizeTextarea()
    },

    /**
     * 
     * @param {String} value [description]
     */
    setCurrentValue (value) {
      if (value === this.currentValue) return
      this.$nextTick(() => {
        this.resizeTextarea()
      })
      this.currentValue = value
      // TODO 'DatePicker', 'TimePicker', 'Cascader', 'Search'
      // if (!findComponentUpward(this, ['DatePicker', 'TimePicker', 'Cascader', 'Search'])) {
      //   this.dispatch('FormItem', 'on-form-blur', this.currentValue)
      // }
    },

    /**
     * 
     */
    resizeTextarea () {
      const autosize = this.autosize
      if (!autosize || this.type !== 'textarea') {
        return false
      }

      const minRows = autosize.minRows
      const maxRows = autosize.maxRows

      this.textareaStyles = calcTextareaHeight(this.$refs.textarea, minRows, maxRows)
    },

    /**
     * 
     * @param  {HTML DOM Event} event 
     */
    onEnter (event) {
      this.$emit('enter', event)
    },

    /**
     * 
     * @param  {HTML DOM Event} event 
     */
    onFocus (event) {
      this.$emit('focus', event)
    },

    /**
     * 
     * @param  {HTML DOM Event} event 
     */
    onBlur (event) {
      this.$emit('blur', event)
      // TODO 'DatePicker', 'TimePicker', 'Cascader', 'Search'
      // if (!findComponentUpward(this, ['DatePicker', 'TimePicker', 'Cascader', 'Search'])) {
      //   this.dispatch('FormItem', 'on-form-blur', this.currentValue)
      // }
    },

    /**
     * 
     * @param  {HTML DOM Event} event 
     */
    onInput (event) {
      let value = event.target.value
      if (this.number) value = Number.isNaN(Number(value)) ? value : Number(value)
      this.$emit('input', value)
      this.setCurrentValue(value)
      this.$emit('value-change', event)
    },

    /**
     * 
     * @param  {HTML DOM Event} event 
     */
    onChange (event) {
      this.$emit('input-change', event)
    },

    /**
     * 
     * @param  {HTML DOM Event} event 
     */
    onIconClick (event) {
      this.$emit('click-icon', event)
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/input'

.x-input
  input()
  &-wrapper
    display inline-block
    width 100%
    position relative
    vertical-align middle

  &-icon
    size 32px input-height-base
    line-height input-height-base
    text-align center
    font-size 16px
    color extra-light-gray
    absolute right
    z-index 1

    &:hover
      color primary-color

  &-icon + &
    padding-right 32px

  &-wrapper--large &-icon
    font-size 18px
    height input-height-large
    line-height input-height-large

  &-wrapper--small &-icon
    size 24px input-height-small
    font-size 14px
    line-height input-height-small

    + .x-input
      padding-right 24px

for theme in themes
  .theme-{theme}
    .x-input
      placeholder(lookup(theme + '-input-placeholder-color'))
      input-color(theme)

    .x-input-group
      input-group-color(theme)

.x-input-group
  input-group()
</style>



