<template>
  <transition :name="transitionName">
    <div :class="classes" :style="styles">
      <div :class="contentClasses" ref="content" v-html="content"></div>
      <span :class="closeIconClasses" @click="close" v-if="closable">
        <icon type="times"></icon>
      </span>
    </div>
  </transition>
</template>

<script>
import Icon from '../../icon/icon'

export default {
  name: 'Notice',

  props: {
    // 
    baseClass: {
      type: String,
      default: ''
    },
    // 
    duration: {
      type: Number,
      default: 2
    },
    // 
    content: {
      type: String,
      default: ''
    },
    // 
    styles: {
      type: Object,
      default () {
        return {
          right: '50%'
        }
      }
    },
    // 
    closable: {
      type: Boolean,
      default: false
    },
    // 
    name: {
      type: String,
      required: true
    },
    // 
    onClose: {
      type: Function
    },
    // 
    className: {
      type: String
    },
    // 
    transitionName: {
      type: String
    }
  },

  components: {
    Icon
  },

  data () {
    return {
      withDesc: false // 
    }
  },

  computed: {
    // 
    prefixClass () {
      return `${this.baseClass}-notice`
    },
    // 
    classes () {
      return [
        this.prefixClass,
        {
          [`${this.className}`]: !!this.className,
          [`${this.prefixClass}--closable`]: this.closable,
          [`${this.prefixClass}--with-desc`]: this.withDesc
        }
      ]
    },
    // 
    contentClasses () {
      return `${this.prefixClass}__content`
    },
    // 
    closeIconClasses () {
      return `${this.prefixClass}__close`
    }
  },

  mounted () {
    this.clearTimer()

    // 
    if (this.duration !== 0) {
      this.timer = setTimeout(() => {
        this.close()
      }, this.duration * 1000)
    }

    // 
    if (this.baseClass === 'x-notice') {
      this.withDesc = this.$refs['content'].querySelectorAll(`.${this.baseClass}__desc`)[0].innerHTML !== ''
    }
  },

  beforeDestroy () {
    this.clearTimer()
  },

  methods: {
    /**
     * 
     */
    close () {
      this.clearTimer()
      this.onClose() // 
      this.$parent.close(this.name) // 
    },

    /**
     * 
     */
    clearTimer () {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
    }
  }
}
</script>



