/**
 * 
 * @param {Number|String|Boolean} value 
 * @param {Array} validList 
 * @return {Boolean}
 */
function oneOf (value, validList) {
  for (let i = 0; i < validList.length; i++) {
    if (value === validList[i]) {
      return true
    }
  }
  return false
}

export default oneOf



