<template>
  <div class="x-slider-component-root" :style="rootStyle" :class="{hide, 'not-allowed': disabled}" @click="onFocus">
    <div class="slider" :style="sliderStyle">
      <div class="line-box">
        <div class="line back-line pointer" @click="onLineClick"></div>
        <div
          class="line selected-line pointer"
          :class="{transition: !dragging}"
          :style="selectedLineStyle"
          @click="onLineClick">
        </div>
      </div>
      <div
        class="circle pointer"
        :class="{transition: !dragging, focus: isFocus}"
        :style="circleStyle"
        @mouseenter="showTooltip"
        @mouseleave="hideTooltip"
        @mousedown="onCircleDown">
      </div>
      <div
        class="tooltip ignore-focus"
        :class="{transition: !dragging}"
        :style="tooltipStyle"
        v-show="isShowTooltip"
        @mouseenter="showTooltip"
        @mouseleave="hideTooltip"
        >
        <!-- <span class="ignore-focus" v-if="disable">{{tooltipLabel}}</span> -->
        <span v-if="isShowTooltip">
          {{tooltip.split('{value}')[0] || ''}}
          <span
            class="tooltip-input ignore-focus"
            :contenteditable="!disabled"
            @focus="onTooltipFocus"
            @blur="onTooltipBlur"
            @keydown="onTooltipEnter"
            @input="onTooltipInput"
            >{{currentValue}}</span>
            {{tooltip.split('{value}')[1] || ''}}
        </span>
        <div class="triangle"></div>
      </div>
    </div>
    <template v-if="isShowLabel">
      <div class="label min"><span>{{min}}{{unit}}</span></div>
      <div class="label max"><span>{{max}}{{unit}}</span></div>
    </template>

    <input
      type="text"
      class="hidden-input"
      @focus="onInputFocus"
      @blur="onInputBlur"
      @keydown="onKeyDown">
  </div>
</template>

<script>
/*
  : qiuguohui
  
  <x-slider
  :value=""
  :disabled=""
  :step=""
  :min=""
  :max=""
  :color=""
  :isShowLabel=""
  :tooltip=" {value}   '{value}%' {value}"
  :unit=""
  @change="value "
  @changed="value ">
  </x-slider>
 */
import elementResizeEvent from 'element-resize-event'
import { checkPropsData } from './utils'
import { addClass, removeClass } from '../../utils/dom/class'

// 
const DISABLE_COLOR = '#afafaf'
export default {
  name: 'Slider',

  props: {
    // 
    value: {
      type: Number,
      default: 0,
      required: true
    },
    // 
    min: {
      type: Number,
      default: 0
    },
    // 
    max: {
      type: Number,
      default: 100
    },
    // 
    step: {
      type: Number,
      default: 1
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    // 
    color: {
      type: String,
      default: '#c0252e'
    },
    // 
    circleRadius: {
      type: Number,
      default: 6
    },
    // 
    isShowLabel: {
      type: Boolean,
      default: true
    },
    // 
    tooltip: {
      type: String,
      default: '{value}'
    },
    // 
    unit: {
      type: String,
      default: ''
    }
  },

  data () {
    return {
      // 
      hide: true,
      // 
      containerWidth: 0,
      // 
      currentValue: 0,
      // 
      dragging: false,
      // 
      mask: {},
      // 
      isShowTooltip: false,
      // 
      isFocus: false,
      tooltipIsFocus: false
    }
  },

  computed: {
    // 
    percent () {
      let {min, max, currentValue} = this
      let d = max - min
      let result = currentValue / d * 100
      if (result > 100) result = 100
      if (result < 0) result = 0
      return result
    },
    // 
    rootStyle () {
      let sliderHeight = this.circleRadius * 2
      let height = this.isShowLabel ? sliderHeight + 2 + 25 : sliderHeight + 2
      let result = {
        height: `${height}px`,
        color: this.disabled ? DISABLE_COLOR : this.color
      }
      return result
    },
    // 
    sliderStyle () {
      let result = {
        height: `${this.circleRadius * 2}px`,
        lineHeight: `${this.circleRadius * 2}px`,
        paddingLeft: `${this.circleRadius}px`,
        paddingRight: `${this.circleRadius}px`
      }
      return result
    },
    // 
    selectedLineStyle () {
      let result = {
        width: `${this.hide ? '0' : this.percent}%`
      }
      return result
    },
    // 
    circleStyle () {
      let d = this.circleRadius * 2
      let length = `${d}px`
      let left = (this.containerWidth - this.circleRadius * 2) * this.percent / 100
      let result = {
        width: length,
        height: length,
        left: `${left}px`,
        transition: this.dragging ? '0s' : ''
      }
      return result
    },
    // 
    tooltipStyle () {
      let left = (this.containerWidth - this.circleRadius * 2) * this.percent / 100 + this.circleRadius
      let result = {
        bottom: `${this.circleRadius * 2 + 10}px`,
        left: `${left}px`
      }
      return result
    },
    // 
    tooltipLabel () {
      return this.tooltip.replace('{value}', this.currentValue)
    }
  },

  watch: {
    // value
    value (val) {
      // 
      this.showTooltip()
      // 
      this.valueChangeHideTooltipTimeId = setTimeout(() => {
        this.hideTooltip()
      }, 1000)
      //   
      if (val === this.currentValue) return
      // props 
      if (!checkPropsData(this)) return
      // val
      this.currentValue = this.correctValue(val)
    },

    currentValue (val) {
      // value value  
      if (val === this.value) return
      this.emitChange(val)
    }
  },

  mounted () {
    // props
    checkPropsData(this) && this.init()
  },

  methods: {
    /**
     * 
     */
    init () {
      // 
      this.currentValue = this.correctValue(this.value)
      // 
      setTimeout(() => {
        this.containerWidth = this.getSliderWidth()
      }, 100)
      // 
      this.listenElementResizeEvent()
    },

    /**
     * 
     */
    listenElementResizeEvent () {
      //  
      elementResizeEvent(this.$el, (e) => {
        window.clearTimeout(this.resizeTid)
        this.resizeTid = setTimeout(() => {
          this.containerWidth = this.getSliderWidth()
        }, 30)
      })
    },

    /**
     * 
     */
    onFocus (e) {
      if (e && e.target.className.indexOf('ignore-focus') > -1) return
      let input = this.$el.getElementsByClassName('hidden-input')[0]
      input.focus()
    },

    /**
     * value  
     */
    correctValue (value) {
      let stepCount = Math.round((value - this.min) / this.step)
      return stepCount * this.step + this.min
    },

    /**
     * dom 
     */
    getSliderWidth () {
      let width = this.$el.getElementsByClassName('slider')[0].clientWidth
      width > 0 && (this.hide = false)
      return width
    },

    /**
     * 
     */
    onLineClick (e) {
      if (this.disabled) return
      let percent = e.offsetX / (this.containerWidth - this.circleRadius * 2)
      let value = (this.max - this.min) * percent
      let correctedValue = this.correctValue(value)
      this.currentValue = correctedValue
    },

    /**
     * 
     * 
     */
    onCircleEnter () {
      this.showTooltip()
    },

    /**
     * 
     * 
     */
    onCircleLeave () {
      if (this.dragging) return
      this.hideTooltip()
    },

    /**
     * 
     */
    onInputFocus (e) {
      this.isFocus = true
    },

    /**
     * 
     */
    onInputBlur (e) {
      this.isFocus = false
    },

    /**
     * 
     */
    onKeyDown (e) {
      switch (e.keyCode) {
        case 37:
          this.decOneStep()
          break
        case 38:
          this.addOneStep()
          break
        case 39:
          this.addOneStep()
          break
        case 40:
          this.decOneStep()
          break
        default:
          break
      }
    },

    /**
     * 
     */
    onTooltipFocus () {
      this.tooltipIsFocus = true
    },

    /**
     * tooltip
     */
    onTooltipBlur (e) {
      let text = e.target.innerText
      let value = text.replace(/\D/g, '') - 0
      if (value <= this.max && value >= this.min) {
        this.currentValue = this.correctValue(value - 0)
        e.target.innerText = this.currentValue
      } else {
        e.target.innerText = this.currentValue
      }
      this.tooltipIsFocus = false
      this.hideTooltip()
    },

    /**
     * tooltip
     */
    onTooltipEnter (e) {
      switch (e.keyCode) {
        case 13:
          e.preventDefault()
          this.onFocus()
          break
        case 38:
          e.preventDefault()
          this.addOneStep()
          break
        case 40:
          e.preventDefault()
          this.decOneStep()
          break
        default:
          break
      }
    },

    /**
     * 
     *  
     */
    onTooltipInput (e) {
      window.clearTimeout(this.onTooltipInputTimeId)
      this.onTooltipInputTimeId = setTimeout(() => {
        let text = e.target.innerText
        let value = text.replace(/\D/g, '') - 0
        if (value <= this.max && value >= this.min) {
          this.currentValue = this.correctValue(value - 0)
          e.target.innerText = this.currentValue
        }
      }, 1000)
    },

    /**
     * 
     */
    addOneStep () {
      let targetValue = this.currentValue + this.step
      if (targetValue > this.max) return
      this.currentValue = targetValue
    },

    /**
     * 
     */
    decOneStep () {
      let targetValue = this.currentValue - this.step
      if (targetValue < this.min) return
      this.currentValue = targetValue
    },

    /**
     * 
     * 
     */
    showTooltip () {
      window.clearTimeout(this.valueChangeHideTooltipTimeId)
      window.clearTimeout(this.tooltipTid)
      this.isShowTooltip = true
    },

    /**
     * 
     */
    hideTooltip () {
      if (this.tooltipIsFocus) return
      this.tooltipTid = setTimeout(() => {
        this.isShowTooltip = false
      }, 500)
    },

    /**
     * 
     * 
     */
    onCircleDown (event) {
      if (this.disabled) return
      this.dragging = true
      let backLine = this.$el.getElementsByClassName('back-line')[0]
      let body = document.body
      addClass(body, 'no-user-select')
      body.addEventListener('mousemove', this.onBodyMouseMove = (e) => {
        if (this.disabled) return
        if (!this.dragging) return
        this.showTooltip()
        let {left, right} = backLine.getBoundingClientRect()
        let d = right - left
        let {pageX} = e
        if (pageX < left) {
          pageX = left
        } else if (pageX > right) {
          pageX = right
        }
        let percent = (pageX - left) / d
        let value = (this.max - this.min) * percent
        let correctedValue = this.correctValue(value)
        this.currentValue = correctedValue
      })
      body.addEventListener('mouseup', this.onBodyMouseUp = this.onDragEnd.bind(this))
      body.addEventListener('mouseleave', this.onBodyMouseleave = this.onDragEnd.bind(this))
    },

    onDragEnd () {
      removeClass(document.body, 'no-user-select')
      document.body.removeEventListener('mousemove', this.onBodyMouseMove)
      document.body.removeEventListener('mouseup', this.onBodyMouseUp)
      document.body.removeEventListener('mouseleave', this.onBodyMouseleave)
      this.dragging = false
      this.hideTooltip()
      this.onFocus()
    },

    /**
     * changechanged
     */
    emitChange (val) {
      this.emit('change', val)
      window.clearTimeout(this.emitTimeId)
      this.emitTimeId = setTimeout(() => {
        this.emit('changed', val)
      }, 300)
    },

    emit (name, ...params) {
      this.$emit(name, ...params)
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-slider-component-root
  position relative
  width 100%
  padding 1px
  box-sizing border-box
  display inline-block
  transition all ease .2s
  &.hide
    opacity 0
  .not-allowed
    cursor not-allowed
  .slider
    position relative
    width 100%
    box-sizing border-box
    opacity 1
    /*&:hover
      opacity 1*/
    .line-box
      position relative
      display inline-block
      width 100%
      height 4px
      top -2px
    .line
      position absolute
      width 100%
      height 0px
      border-top 4px solid
    .circle
      position absolute
      top 0
      left 0
      border 2px solid
      border-radius 100px
      box-sizing border-box
      &.focus
        box-shadow 0 0 5px 0px
      &:hover
        transform scale(1.5)
    .tooltip
      position absolute
      left 50%
      bottom 0
      width auto
      transform translate(-50%)
      background rgba(0,0,0,0.5)
      padding 10px 20px
      border-radius 5px
      font-size 12px
      font-weight 100
      text-align center
      white-space nowrap
      z-index zindex-tooltip
      .tooltip-input
        /*transform translate(10)*/
        margin 0
        padding 0
      .triangle
        position absolute
        top 100%
        left 50%
        transform translate(-50%)
        triangle(rgba(0,0,0,0.5), 10px)


  &>.label
    position absolute
    bottom 0
    &.min
      left 0
    &.max
      right 0

  .hidden-input
    position absolute
    top -5000px
    left -5000px
    width 1px
    height 1px
    opacity 0

  .pointer
    cursor pointer

  &.not-allowed
  .not-allowed
    cursor not-allowed
    .pointer
      cursor not-allowed

  .transition
    transition all ease-in-out 0.3s

.no-user-select
  user-select none

for theme in themes
  .theme-{theme}
    .x-slider-component-root
      .line.back-line
        color lookup(theme + '-back-line-color')
      .circle
        background lookup(theme + '-circle-background-color')
      .tooltip-input
        color lookup(theme + '-tooltip-input-color')
      &>.label
        color lookup(theme + '-label-color')
      .tooltip
        color lookup(theme + '-tooltip-color')

</style>



