<script>
import { ICON_PREFIX } from '../../config'
import Notification from '../base/notification'

const CLASS_PREFIX = 'x'
const baseClass = `${CLASS_PREFIX}-message`
const prefixKey = `${CLASS_PREFIX}_message_key_`
// TODO  loading 
const iconTypes = {
  'info': 'info-circle',
  'success': 'check-circle',
  'warning': 'exclamation-circle',
  'error': 'times-circle'
}

let top // 
let defaultDuration = 1.5 // 
let name = 1 // 
let messageInstance // 

/**
 * 
 */
function getMessageInstance () {
  messageInstance = messageInstance || Notification.newInstance({
    baseClass: baseClass,
    styles: {
      top: `${top}px`
    }
  })

  return messageInstance
}

/**
 * 
 * @params {String} type     [normal|info|success|warning|error]  normal
 * @params {Object} options 
 * @return {Function}
 */
function notice (content, duration = defaultDuration, type, onClose) {
  onClose = onClose || function () {}

  const iconType = iconTypes[type]

  let instance = getMessageInstance()

  instance.notice({
    name: `${prefixKey}${name}`,
    duration: duration,
    styles: {},
    transitionName: 'move-up',
    content: `
      <div class="${baseClass}--custom-content ${baseClass}--${type}">
        <i class="${ICON_PREFIX} ${ICON_PREFIX}-${iconType}"></i>
        <span>${content}</span>
      </div>
    `,
    onClose: onClose
  })

  return (function () {
    let target = name++

    return function () {
      instance.remove(`${prefixKey}${target}`)
    }
  })()
}

export default {
  /**
   * 
   * @params {String} content 
   * @params {Number} duration 
   * @params {Function} onClose 
   */
  info (content, duration, onClose) {
    return notice(content, duration, 'info', onClose)
  },

  /**
   * 
   * @params {String} content 
   * @params {Number} duration 
   * @params {Function} onClose 
   */
  success (content, duration, onClose) {
    return notice(content, duration, 'success', onClose)
  },

  /**
   * 
   * @params {String} content 
   * @params {Number} duration 
   * @params {Function} onClose 
   */
  warning (content, duration, onClose) {
    return notice(content, duration, 'warning', onClose)
  },

  /**
   * 
   * @params {String} content 
   * @params {Number} duration 
   * @params {Function} onClose 
   */
  error (content, duration, onClose) {
    return notice(content, duration, 'error', onClose)
  },

  /**
   * 
   * @param {Object} options 
   */
  config (options) {
    if (options.top) {
      top = options.top
    }
    if (options.duration) {
      defaultDuration = options.duration
    }
  },

  /**
   * 
   */
  destroy () {
    let instance = getMessageInstance()
    messageInstance = null
    instance.destroy()
  }
}
</script>



